* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Inter, Times New Roman, Times, serif;
}

html, body {
  width: 100%;
  height: 100%;
}

body {
  color: #000;
  background-color: #fff;
  transition: background-color .3s, color .3s;
}

body.dark-mode {
  color: #fff;
  background-color: #121212;
}

body.dark-mode .formContainer {
  color: #fff;
  background-color: #262626;
}

.messageContainer {
  background-color: #fff;
}

.formContainer {
  color: #fff;
  background-color: #8c8c8c;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.formContainer.dark-mode {
  color: #fff;
  background-color: #444;
}

.formContainer form {
  align-items: center;
  gap: 10px;
  display: flex;
}

.formContainer label {
  color: #fff;
  font-size: 16px;
}

.formContainer input, .formContainer textarea {
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 5px;
}

.formContainer input {
  width: 150px;
}

.formContainer textarea {
  resize: none;
  width: 200px;
  height: 30px;
  font-size: 13px;
}

#submitButton {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 3px;
  padding: 5px 10px;
  font-weight: bold;
}

#submitButton.dark-mode {
  color: #fff;
  background-color: #444;
}

#submitButton:hover {
  background-color: #0056b3;
  scale: 110%;
}

#darkModeToggle {
  cursor: pointer;
  color: inherit;
  background: none;
  border: none;
  font-size: 20px;
  transition: transform .2s;
}

#darkModeToggle:hover {
  transform: scale(1.2);
}

.message {
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 10px;
}

@keyframes shake {
  0% {
    transform: translateX(0);
  }

  10% {
    transform: translateX(-5px);
  }

  20% {
    transform: translateX(5px);
  }

  30% {
    transform: translateX(-5px);
  }

  40% {
    transform: translateX(5px);
  }

  50% {
    transform: translateX(-5px);
  }

  60% {
    transform: translateX(5px);
  }

  70% {
    transform: translateX(-5px);
  }

  80% {
    transform: translateX(5px);
  }

  90% {
    transform: translateX(-5px);
  }

  100% {
    transform: translateX(0);
  }
}

.shake {
  animation: 1s shake;
}

.notification {
  color: #fff;
  z-index: 9999;
  background-color: #4caf50;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 2px 10px #0000001a;
}

.birthdayDiv {
  z-index: 100000;
  color: #ff13b4;
  background-color: #0056b3;
  font-size: 100px;
  display: none;
  position: absolute;
  top: 70px;
  left: 0;
}

.birthdayDiv h1 {
  animation: 1s infinite bounce;
}

.search-container {
  align-items: center;
  margin: 15px 0;
  padding: 0 20px;
  transition: background-color .3s, color .3s;
  display: flex;
  position: relative;
}

#messageSearch {
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  width: 100%;
  padding: 8px 12px 8px 35px;
  font-size: 16px;
  transition: border-color .3s;
}

.search-icon {
  color: #777;
  position: absolute;
  left: 30px;
}

#messageSearch:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px #007bff4d;
}

.search-container.dark-mode #messageSearch {
  color: #fff;
  background-color: #333;
  border-color: #555;
}

.search-container.dark-mode .search-icon {
  color: #aaa;
}

#searchResultCount {
  color: #777;
  margin-left: 10px;
  font-size: 14px;
}

.search-container.dark-mode #searchResultCount {
  color: #bbb;
}

.message-item {
  transition: opacity .3s;
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }

  25% {
    transform: translateY(-20px);
  }

  50% {
    transform: translateY(0);
  }

  75% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}

.bounceEffect {
  animation: 1s infinite bounce;
}
